.speechBubbleLeft {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: start;
  background: #f2fdc6;
  border-radius: 0 0.4em 0.4em 0.4em;
  min-height: 90px;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.speechBubbleLeft:before {
  content: '';
  position: absolute;
  left: 0;
  top: 7px;
  width: 0;
  height: 0;
  border: 13px solid transparent;
  border-right-color: #f2fdc6;

  border-left: 0;
  border-top: 0;
  margin-top: -6.5px;
  margin-left: -13px;
}
.speechBubbleStartLeft {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: start;
  background: #f2fdc6;
  border-radius: 0.4em 0.4em 0.4em 0.4em;
  min-height: 90px;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.speechBubbleStartLeft:before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  width: 0;
  height: 0;

  border: 9px solid transparent;
  border-right-color: #f2fdc6;
  border-left: 0;
  margin-top: -9px;
  margin-left: -9px;
}
.speechBubbleStartRight {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: start;
  background: skyblue;
  border-radius: 0.4em 0.4em 0.4em 0.4em;
  min-height: 90px;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.speechBubbleStartRight:before {
  content: '';
  position: absolute;
  right: 0;
  top: 50%;
  width: 0;
  height: 0;
  border: 9px solid transparent;
  border-left-color: skyblue;
  border-right: 0;
  margin-top: -9px;
  margin-right: -9px;
}
.speechBubbleRight {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: start;
  background: skyblue;
  border-radius: 0.4em 0 0.4em 0.4em;
  min-height: 90px;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.speechBubbleRight:after {
  content: '';
  position: absolute;
  right: 0;
  top: 5px;
  width: 0;
  height: 0;
  border: 9px solid transparent;
  border-left-color: skyblue;
  border-right: 0;
  border-top: 0;
  margin-top: -4.5px;
  margin-right: -9px;
}
